.delete-modal .modal-overlay {
  backdrop-filter: blur(4px);
  animation: fadeIn 0.3s ease-out;
}
.delete-modal .modal-content {
  animation: slideUp 0.3s ease-out;
}
.delete-modal .warning-icon {
  animation: pulse 2s infinite;
}
.delete-modal .delete-button {
  transition: all 0.2s ease;
}
.delete-modal .delete-button:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(239, 68, 68, 0.3);
  background-color: #dc2626;
}
.delete-modal .delete-button:active {
  transform: translateY(0);
}
.delete-modal .cancel-button {
  transition: all 0.2s ease;
}
.delete-modal .cancel-button:hover {
  background-color: #e5e7eb;
}
.delete-modal .close-button {
  transition: all 0.2s ease;
}
.delete-modal .close-button:hover {
  color: #6b7280;
  transform: scale(1.1);
}
.delete-modal .info-box {
  animation: slideDown 0.2s ease-out;
  background: linear-gradient(135deg, #f9fafb, #f3f4f6);
}
.delete-modal .warning-content {
  animation: slideIn 0.3s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(-10px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
}
